'use client';

import { Home, Droplets, Sun, Sprout, ChevronRight } from 'lucide-react';

export default function PlantPage() {
  return (
    <div className="max-w-md mx-auto min-h-screen bg-white flex flex-col">
      {/* Header */}
      <header className="p-4 border-b border-gray-100">
        <h1 className="text-xl font-semibold">My Plants</h1>
      </header>

      {/* Main Content */}
      <main className="flex-1 p-4 overflow-y-auto">
        <div className="bg-green-50 rounded-2xl p-6 mb-6">
          <div className="flex items-center justify-between mb-4">
            <div>
              <h2 className="text-2xl font-bold">Monstera</h2>
              <p className="text-green-600">Thriving</p>
            </div>
            <div className="w-20 h-20 bg-green-100 rounded-full flex items-center justify-center">
              <Sprout className="w-10 h-10 text-green-600" />
            </div>
          </div>

          <div className="space-y-4">
            <div className="flex items-center justify-between p-3 bg-white rounded-xl">
              <div className="flex items-center gap-3">
                <div className="p-2 bg-blue-100 rounded-lg">
                  <Droplets className="w-5 h-5 text-blue-500" />
                </div>
                <div>
                  <p className="font-medium">Water</p>
                  <p className="text-sm text-gray-500">Every 7 days</p>
                </div>
              </div>
              <ChevronRight className="w-5 h-5 text-gray-400" />
            </div>

            <div className="flex items-center justify-between p-3 bg-white rounded-xl">
              <div className="flex items-center gap-3">
                <div className="p-2 bg-yellow-100 rounded-lg">
                  <Sun className="w-5 h-5 text-yellow-500" />
                </div>
                <div>
                  <p className="font-medium">Light</p>
                  <p className="text-sm text-gray-500">Bright, indirect light</p>
                </div>
              </div>
              <ChevronRight className="w-5 h-5 text-gray-400" />
            </div>
          </div>
        </div>

        <div className="mb-6">
          <h3 className="text-lg font-semibold mb-3">Care Schedule</h3>
          <div className="bg-white rounded-xl p-4 shadow-sm">
            <div className="flex items-center justify-between mb-3">
              <span className="text-gray-500">Next watering</span>
              <span className="font-medium">In 2 days</span>
            </div>
            <div className="h-2 bg-gray-100 rounded-full overflow-hidden">
              <div className="h-full bg-blue-500 rounded-full" style={{ width: '60%' }}></div>
            </div>
          </div>
        </div>
      </main>

      {/* Bottom Navigation */}
      <nav className="flex items-center justify-around p-4 border-t border-gray-100">
        <button className="flex flex-col items-center">
          <Home className="w-6 h-6 text-green-600" />
          <span className="text-xs mt-1 text-green-600">Home</span>
        </button>
        <button className="flex flex-col items-center">
          <Sprout className="w-6 h-6 text-gray-400" />
          <span className="text-xs mt-1 text-gray-400">Plants</span>
        </button>
        <button className="flex flex-col items-center">
          <div className="w-12 h-12 bg-green-600 rounded-full -mt-6 flex items-center justify-center text-white">
            <span className="text-xl">+</span>
          </div>
        </button>
        <button className="flex flex-col items-center">
          <div className="w-6 h-6 bg-gray-300 rounded-full"></div>
          <span className="text-xs mt-1 text-gray-400">Profile</span>
        </button>
      </nav>
    </div>
  );
}
